
.user_list li {
  border-bottom: 1px solid #eee;
}
.user_list .author_wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 15px;
}
.user_list .author_wrap .avatar_wrap {
  width: 30px;
  height: 30px;
  border: 1px solid #f7f7f7;
  margin-right: 10px;
  border-radius: 50%;
}
.user_list .author_wrap .avatar_wrap .avatar {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: #eee;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}
.user_list .author_wrap .info_wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
.user_list .author_wrap .info_wrap .nickname {
  color: #666;
  font-weight: 500;
  height: 16px;
  line-height: 16px;
  font-size: 14px;
}
.user_list .author_wrap .info_wrap .list_title {
  display: inline-block;
  padding: 10px 0;
  font-size: 15px;
  line-height: 1.5;
  color: #222;
}
.user_list .author_wrap .extra_wrap {
  font-size: 14px;
  color: #888;
}
.list-null {
  text-align: center;
  line-height: 120px;
  font-size: 14px;
}



