
.login_wrap .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.login_wrap .form {
  padding: 0 10%;
  width: 80%;
  margin-top: -60px;
}
.login_wrap .form:focus {
  outline: none;
}
.login_wrap .form .item {
  padding: 20px 0;
}
.login_wrap .form .item .input {
  width: 100%;
  box-sizing: border-box;
  padding: 5px 15px;
  border: 1px solid #ccc;
  outline: none;
  line-height: 28px;
  font-size: 14px;
  border-radius: 5px;
}
.login_wrap .form .item .input:focus {
  border-color: rgba(0, 0, 0, 0.5);
}
.login_wrap .form .item .btn {
  width: 100%;
  background-color: #80bd01;
  color: #fff;
  border: 1px solid #76ae01;
  line-height: 38px;
  border-radius: 5px;
  font-size: 14px;
}
.login_wrap .form .item .btn:active {
  background-color: #6fa401;
  box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.3);
}
.login_wrap .form .item .btn:focus {
  outline: none;
}



