
* {
  outline: none;
}
.publish_wrap .item .icon-edit {
  font-size: 24px;
  color: #80bd01;
}
.publish_wrap .item:active {
  background-color: rgba(0, 0, 0, 0.1);
}
.publish_wrap .form .title {
  padding: 5px 15px;
}
.publish_wrap .form .title input {
  box-sizing: border-box;
  width: 100%;
  padding: 0 5px;
  font-size: 14px;
  line-height: 30px;
}
.publish_wrap .form .select {
  box-sizing: border-box;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.publish_wrap .form .select select {
  padding: 10px 5px;
  width: 100%;
}
.publish_wrap .form .con {
  padding: 5px 10px;
}
.publish_wrap .form .con textarea {
  border: none;
  width: 100%;
  padding: 0 5px;
  resize: none;
}



