
.user {
  width: 100%;
}
.user .header_bg {
  display: block;
  width: 100%;
  height: 160px;
  background-color: #5FC3E4;
  background-image: linear-gradient(120deg, #E55D87, #5FC3E4);
}
.user .avatar_wrap {
  text-align: center;
  margin-top: -40px;
}
.user .avatar_wrap .avatar {
  display: inline-block;
  width: 80px;
  height: 80px;
  background-color: #eee;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  border: 3px solid #f7f7f7;
}
.user .nickname {
  text-align: center;
  font-size: 22px;
  margin: 10px 0;
}
.user .user_info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 15px;
}
.user .user_info span {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 14px;
  line-height: 16px;
}
.user .user_info span:nth-child(1) {
  text-align: left;
}
.user .user_info span:nth-child(2) {
  text-align: right;
}
.user_ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-top: 10px;
}
.user_ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 38px;
  text-align: center;
  border-right: 1px solid #ddd;
}
.user_ul li.on {
  color: #80bd01;
}
.user_ul li:nth-child(2) {
  border: none;
}



