
.my_message_wrap .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 15px;
  border-bottom: 1px solid #ddd;
  word-break: break-all;
}
.my_message_wrap .avatar_wrap {
  width: 40px;
  height: 100%;
  margin-right: 10px;
}
.my_message_wrap .avatar_wrap img {
  width: 36px;
  height: 36px;
  border: 2px solid #f7f7f7;
  border-radius: 50%;
  background-color: #eee;
}
.my_message_wrap .content_wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
.my_message_wrap .content_wrap .top {
  line-height: 2;
}
.my_message_wrap .content_wrap .top .nickname {
  font-size: 14px;
  font-weight: bold;
}
.my_message_wrap .content_wrap .top time {
  font-size: 13px;
  color: #888;
}
.my_message_wrap .content_wrap .mid .topic_title {
  color: #80bd01;
}
.my_message_wrap .content_wrap .bot {
  margin-top: 10px;
  padding: 0 5px;
  border-radius: 5px;
  background-color: #f5f5f5;
}



