
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  border-top: 1px solid #d4d4d4;
}
.footer ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 5px 0;
}
.footer ul .footer_item {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.footer ul .footer_item:active {
  background-color: #f5f5f5;
}
.footer ul .active a {
  color: #80bd01;
}
.footer ul a {
  position: relative;
  display: block;
  color: #222;
}
.footer ul i {
  display: block;
  line-height: 24px;
}
.footer ul em {
  font-style: normal;
  font-size: 13px;
  line-height: 16px;
}
.footer ul .count {
  position: absolute;
  top: 0;
  left: 50%;
  padding: 2px 5px;
  border-radius: 15px;
  font-size: 12px;
  background-color: red;
  color: #fff;
}



