
.reply-box {
  padding: 50px 15px;
}
.reply-box .text {
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.reply-box .text textarea {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  font-size: 13px;
  border: none;
  resize: none;
  outline: none;
}
.reply-box .btn_wrap {
  text-align: right;
  margin-top: 10px;
}
.reply-box .btn_wrap .btn {
  display: inline-block;
  padding: 5px 15px;
  background-color: #80bd01;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #76ae01;
}
.reply-box .btn_wrap .btn:active {
  background-color: #6fa401;
}



